<template>
  <div>
    <v-chip
      @click="moveBack"
      style="position: absolute; top: 0; left: 10px; z-index: 50;"
    >
        
    </v-chip>

    <v-tabs v-model="tabs" centered class="mt-15">
      <!-- v-tabsdml v-model="tabs"    tabs  -->
      <!-- <v-tab v-for="n in 3" :key=n>   for  -->
      <v-tab> </v-tab>
      <v-tab> </v-tab>
      <v-tab></v-tab>

      <v-tabs-items v-model="tabs" class="mt-15">
        <!--     tabs   v-tab-item   -->
        <!-- ' '  () -->
        <v-tab-item>
          <v-container>
            <!--  (    -->
            <v-row no-gutters justify="center">
              <v-col cols="12" md="4">
                <v-textarea
                label="Your Answer"
                no-resize
                rows="5"
                :value="yourAnswer"
                readonly
                outlined
                class="wronganswer"
                ></v-textarea>
              </v-col>

              <v-col cols="0" md="1"></v-col>

              <v-col cols="12" md="4">
                <v-textarea
                  label="Correct Answer"
                  no-resize
                  rows="5"
                  :value="correctAnswer"
                  readonly
                  outlined
                ></v-textarea>
              </v-col>

              <v-col cols="0" md="5"></v-col>

              <v-col cols="12" md="4"
                v-if="error"
              >
                <v-textarea
                  label="Error Infomation"
                  no-resize
                  rows="5"
                  :value="error"
                  readonly
                  outlined
                ></v-textarea>
              </v-col>

            </v-row>
          </v-container>
        </v-tab-item>

        <!-- ' '   -->
        <v-tab-item>
          <v-btn
            icon
            middle
            right
            fixed
            fab
          >
            <v-img
              width="60px"
              src="@/assets/img/watting_cogi.png"
              @click="isAnswer = !isAnswer"
            />
          </v-btn>
          <v-container>
            <v-row no-gutters justify="center">
              <v-col cols="12" sm="8" md="4">
                <v-textarea
                  label="Your Code"
                  no-resize
                  rows="10"
                  :value="yourCode"
                  readonly
                  outlined
                ></v-textarea>
              </v-col>
              <v-col cols="0" sm="1"></v-col>

              <v-col cols="12" sm="8" md="4"
                v-show="isAnswer"
              >
                <v-textarea
                  label="Correct Code"
                  no-resize
                  rows="10"
                  :value="correctCode"
                  readonly
                  outlined
                ></v-textarea>
              </v-col>
              <v-col cols="12" sm="8" md="4"
                v-show="!isAnswer"
              >
              </v-col>
            </v-row>
          </v-container>
        </v-tab-item>

        <!-- ''   -->
        <v-tab-item>
          <v-container>
            <v-row>
              <v-carousel v-model="cmodel">
                <v-carousel-item
                  v-for="i in imageNumber"
                  :key="i"
                >
                  <v-sheet
                    height="100%"
                    tile
                  >
                    <v-row
                      class="fill-height"
                      align="center"
                      justify="center"
                    >
                      <div class="display-3" v-if="imageContent">
                        <v-img :src="imageContent[i-1]" />
                      </div>
                    </v-row>
                  </v-sheet>
                </v-carousel-item>
              </v-carousel>
            </v-row>
          </v-container>
        </v-tab-item>
      </v-tabs-items>
    </v-tabs>
  </div>
</template>

<script>
import axios from 'axios';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: 'SolveResult',
  data: () => {
    return {
      tabs: null,
      description: false,
      isCorrect: false, //  
      error: '',
      isAnswer: false,  //   
      yourAnswer: '   \n  ',
      correctAnswer: '   ',
      yourCode: '   \n   ',
      correctCode: '',
      imageNumber: 0,
      imageContent: '',
      cmodel: 0,
      colors: [
        'primary',
        'secondary',
        'yellow darken-2',
        'red',
        'orange',
      ],
    }
  },
  methods: {
    moveBack() {
      history.back();
    }
  },
  created() {
    const resultInfo = {
      token: localStorage.getItem('jwt'),
      solved_problem_number: this.$route.params.resultnumber,
    }

    axios
      .post(`${SERVER_URL}/codeAPI/result`, resultInfo)
      .then(response => {
        console.log(response);
        // answer : true
        // cpuTime: "0.02"
        // error: null
        // memory: "5308"
        // statusCode: "200"
        this.isCorrect = response.data.answer;
        if (this.isCorrect) {
          const answer = document.querySelector('.wronganswer');
          answer.classList.add('goodanswer')
        }
        this.error = response.data.error;

        if (this.isCorrect) this.isAnswer = true;

        this.yourAnswer = response.data.my_output;
        this.correctAnswer = response.data.correct_output;
        this.yourCode = response.data.my_code;
        this.correctCode = response.data.correct_code;
        if (!this.correctCode) {
          this.correctCode = '      \n    !'
        }
        this.imageNumber = response.data.img_number;
        this.imageContent = response.data.img_content;
      })
      .catch(error => {
        if (error.response.status === 401) {
          alert(" .");
          this.$emit("expireLogin");
        } else if (error.response.status === 403) {
          alert(" .");
          this.$router.push({ name: 'Home' });
        } else {
          console.log(error);
        }
      });
  }
}
</script>

<style>
div[class*='wronganswer'] > div[class='v-input__control'] > div[class="v-input__slot"] {
  background: lightsalmon;
}
div[class*='goodanswer'] > div[class='v-input__control'] > div[class="v-input__slot"] {
  background: lightseagreen;
}
</style>