<template>
  <div>
    <!--  -->
    <v-sheet color="#FFFFFF" height="300" dark align="center">
      <v-row class="fill-height" align="center" justify="center">
        <div class="display-3">
          <v-img src="@/assets/img/banner/mypage_banner.png" max-width="1000px">
            <v-container fill-height fluid>
              <div style="margin-left: 43%">
                <div class="banner text-center" style="font-size: 40px">
                   
                </div>
              </div>
            </v-container>
          </v-img>
        </div>
      </v-row>
    </v-sheet>

    <!--  -->
    <v-tabs fixed-tabs v-model="tabs" background-color="indigo" dark>
      <v-tab>  </v-tab>
      <v-tab>     </v-tab>
      <v-tab v-if="userInfo.stat == ''">
          
      </v-tab>
      <v-tab v-else>    </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tabs" class="my-15">
      <!--    -->
      <v-tab-item>
        <template style="text_align: center">
          <v-card class="mx-auto my-10" max-width="80%" min-height="400px">
            <img :src="userInfo.profileImg" width="10%" style="margin-top: 50px" />
            <v-card-text style="font-size:1.5em">
              <div class="text--primary">
                <div class="mb-10">{{ userInfo.name }}  ({{ userInfo.stat }})</div>

                <v-simple-table style="width:50%; margin-left:30%; text-align:left">
                  <tr>
                    <td><v-icon color="#FFB911">mdi-paw</v-icon></td>
                    <td><v-icon color="#FFB911">mdi-paw</v-icon>{{ userInfo.email }}</td>
                  </tr>

                  <tr>
                    <td><v-icon color="#6F85FF">mdi-paw</v-icon>SNS</td>
                    <td><v-icon color="#6F85FF">mdi-paw</v-icon>{{ userInfo.sns }}</td>
                  </tr>

                  <tr>
                    <td><v-icon color="#FF4D55">mdi-paw</v-icon></td>
                    <td>
                      <v-icon color="#FF4D55">mdi-paw</v-icon
                      >{{ userInfo.joindate.substring(0, 10) }}
                    </td>
                  </tr>
                </v-simple-table>
              </div>
            </v-card-text>
            <br />

            <!--   -->
            <template>
              <v-row justify="center">
                <v-dialog v-model="dialog" max-width="500">
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn color="primary" dark v-bind="attrs" v-on="on">
                      <v-icon>mdi-pencil</v-icon>
                    </v-btn>
                  </template>
                  <v-card class="px-5 py-1">
                    <v-text-field
                      label=" "
                      type="password"
                      v-model="password"
                      prepend-icon="mdi-lock"
                    ></v-text-field>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="green darken-1" text @click="checkPassword">
                        
                      </v-btn>
                      <v-btn color="green darken-1" text @click="dialog = false">
                        
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-row>
            </template>
            <br /><br />

            <!--    -->
            <v-expand-transition>
              <v-card
                v-if="reveal"
                class="transition-fast-in-fast-out v-card--reveal"
                style="height: 100%"
              >
                <br /><br />

                <v-row>
                  <v-col cols="2"></v-col>
                  <v-col cols="4">
                    <img
                      :src="userInfo.profileImg"
                      width="170px"
                      style="border: 3px solid gold; border-radius: 10px"
                    />
                    <v-file-input
                      accept="image/*"
                      label="  "
                      filled
                      prepend-icon="mdi-pencil"
                      id="photo"
                      enctype="multipart/form-data"
                      @change="Preview_image"
                      v-model="image"
                    ></v-file-input>
                    <br />
                  </v-col>
                  <v-col cols="4">
                    <v-textarea
                      label=""
                      auto-grow
                      outlined
                      rows="1"
                      row-height="15"
                      v-model="newName"
                    ></v-textarea>
                    <v-text-field
                      type="password"
                      :rules="passwordRules"
                      label=""
                      auto-grow
                      outlined
                      rows="1"
                      row-height="15"
                      v-model="newPassword"
                    ></v-text-field>
                    <v-text-field
                      type="password"
                      label=" "
                      auto-grow
                      outlined
                      rows="1"
                      row-height="15"
                      v-model="newPassword2"
                    ></v-text-field>
                  </v-col>
                </v-row>
                <br />

                <v-btn class="ma-2" color="orange darken-2" dark @click="editCancel">
                  <v-icon dark left> mdi-arrow-left </v-icon>
                </v-btn>

                <v-btn class="ma-2" color="purple" dark @click="updateUser">
                  <v-icon dark> mdi-wrench </v-icon>
                </v-btn>

                <v-card-text class="mt-10" style="cursor: pointer" @click="deleteUser"
                  >|  |</v-card-text
                >
              </v-card>
            </v-expand-transition>
          </v-card>
          <br />
        </template>
      </v-tab-item>

      <!--     -->
      <v-tab-item style="text_align: center">
        <MySolvedProblem :userInfo="userInfo" />
      </v-tab-item>

      <!--    -->
      <v-tab-item>
        <MyQuestion :userInfo="userInfo" />
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import MySolvedProblem from '../../components/user/MySolvedProblem.vue';
import MyQuestion from '../../components/user/MyQuestion.vue';
import axios from 'axios';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;
const SECRET_KEY = process.env.VUE_APP_SECRET_KEY;

export default {
  name: 'UserProfile',
  data: () => {
    return {
      userInfo: {
        name: ' ',
        email: ' ',
        joindate: ' ',
        sns: '',
        stat: '',
        number: '',
        profileImg: '',
      },
      image: '',
      tabs: null,
      reveal: false,
      dialog: false,
      isSourceCode: false,
      sourceTitle: '',
      sourceCode: '',
      password: '',
      newName: '',
      newPassword: '',
      newPassword2: '',
      passwordRules: [
        (v) => !!v || ' .',
        (v) =>
          /^(?=.*[a-z])(?=.*[0-9]).{8,16}$/.test(v) ||
          ',   8-16  ',
      ],
    };
  },
  components: {
    MySolvedProblem,
    MyQuestion,
  },
  methods: {
    setToken() {
      const token = localStorage.getItem('jwt');
      if (token) {
        return token;
      }
    },
    setUserInfo() {
      axios
        .post(`${SERVER_URL}/user/info`, { token: localStorage.getItem('jwt') })
        .then((response) => {
          this.userInfo.name = response.data.name;
          this.userInfo.email = response.data.email;
          this.userInfo.joindate = response.data.created_at;
          this.userInfo.number = response.data.user_number;
          this.userInfo.profileImg = response.data.profile_content;
          if (response.data.oauth == 'kakao') this.userInfo.sns = '  ';
          else this.userInfo.sns = '  ';

          if (response.data.admin == true) {
            this.userInfo.stat = '';
          }

          if (response.data.teach == true) {
            if (response.data.admin) this.userInfo.stat += ', ';
            else this.userInfo.stat = '';
          }

          if (!response.data.admin && !response.data.teach) {
            this.userInfo.stat = '';
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },

    editCancel() {
      this.newName = '';
      this.newPassword = '';
      this.newPassword2 = '';
      this.reveal = false;
    },

    Preview_image() {
      this.userInfo.profileImg = URL.createObjectURL(this.image);
    },

    updateUser() {
      if (this.newPassword != this.newPassword2) {
        alert('  .');
        return;
      }

      const crypto = require('crypto');
      const form = {
        password: crypto
          .createHmac('sha256', SECRET_KEY)
          .update(this.newPassword)
          .digest('base64'),
      };

      const fd = new FormData();
      fd.append('name', this.newName);
      fd.append('password', form.password);
      fd.append('token', localStorage.getItem('jwt'));
      fd.append('email', this.userInfo.email);
      if (document.getElementById('photo').files[0]) {
        fd.append('photo', document.getElementById('photo').files[0]);

        console.log(this.fileName);
        console.log(fd.get('photo'));

        if (confirm(' ?')) {
          axios
            .put(`${SERVER_URL}/user`, fd)
            .then((response) => {
              console.log(response);
              this.setUserInfo();

              alert('  !');

              this.newName = '';
              this.newPassword = '';
              this.newPassword2 = '';
              this.reveal = false;
            })
            .catch((error) => {
              alert('  ');
              console.log(error);
            });
        }
      } else {
        if (confirm(' ?')) {
          axios
            .put(`${SERVER_URL}/user/noimage`, fd)
            .then((response) => {
              console.log(response);
              this.setUserInfo();

              alert('  !');

              this.newName = '';
              this.newPassword = '';
              this.newPassword2 = '';
              this.reveal = false;
            })
            .catch((error) => {
              alert('  ');
              console.log(error);
            });
        }
      }
    },

    deleteUser() {
      if (confirm(' ?')) {
        axios
          .delete(`${SERVER_URL}/user`, {
            data: { token: localStorage.getItem('jwt') },
          })
          .then(() => {
            alert('\n   .\n\n\n -   -');

            //   
            localStorage.removeItem('jwt');
            localStorage.removeItem('user_number');
            localStorage.removeItem('name');

            this.$router.push({ name: 'Home' });
          })
          .catch((error) => {
            alert('\n  . \n\n\n  \n...');
            console.log(error);
          });
      }
    },
    checkPassword() {
      const crypto = require('crypto');
      const form = {
        password: crypto
          .createHmac('sha256', SECRET_KEY)
          .update(this.password)
          .digest('base64'),
      };

      axios
        .post(`${SERVER_URL}/user/info`, {
          token: localStorage.getItem('jwt'),
        })
        .then((response) => {
          if (form.password == response.data.password) {
            this.dialog = false;
            this.reveal = true;
          } else {
            alert('  ');
          }
        })
        .catch((error) => {
          console.log(error);
        });

      this.password = '';
    },
  },

  created() {
    const userKey = {
      token: this.setToken(),
    };

    axios
      .post(`${SERVER_URL}/user/info`, userKey)
      .then((response) => {
        this.userInfo.name = response.data.name;
        this.userInfo.email = response.data.email;
        if (response.data.oauth) {
          this.userInfo.sns = response.data.oauth;
        }
      })
      .catch((error) => {
        if (error.response.status === 401) {
          alert(' .');
          this.$emit('expireLogin');
        } else {
          console.log(error);
        }
      });

    this.setUserInfo();
  },
};
</script>

<style scoped>
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}

.v-data-table > .v-data-table__wrapper > table > tbody > tr > th,
.v-data-table > .v-data-table__wrapper > table > thead > tr > th,
.v-data-table > .v-data-table__wrapper > table > tfoot > tr > th {
  font-size: 20px;
}
</style>
