<template>
  <v-col cols="12" sm="6" md="4">

    <h2><v-icon :color="color">mdi-book-open-page-variant</v-icon> {{ basic.name }}</h2><br>

    <!-- video v-bind  src     .... -->
    <v-hover>
    <template v-slot:default="{ hover }">
    <v-card  @click="moveTopic" height="260px" elevation="0">

      <video v-if="basic.name==''" height="100%" autoplay muted loop src="@/assets/video/variable.mp4" type="video/mp4"></video>
      <video v-if="basic.name==''" height="100%" autoplay muted loop src="@/assets/video/datatype.mp4" type="video/mp4"></video>
      <video v-if="basic.name==''" height="100%" autoplay muted loop src="@/assets/video/operator.mp4" type="video/mp4"></video>
      <video v-if="basic.name==''" height="100%" autoplay muted loop src="@/assets/video/input.mp4" type="video/mp4"></video>
      <video v-if="basic.name==''" height="100%" autoplay muted loop src="@/assets/video/output.mp4" type="video/mp4"></video>
      <video v-if="basic.name=='if '" height="100%" autoplay muted loop src="@/assets/video/if.mp4" type="video/mp4"></video>
      <video v-if="basic.name=='for '" height="100%" autoplay muted loop src="@/assets/video/for.mp4" type="video/mp4"></video>
      <video v-if="basic.name=='while '" height="100%" autoplay muted loop src="@/assets/video/while.mp4" type="video/mp4"></video>
      <video v-if="basic.name=='1 '" height="100%" autoplay muted loop src="@/assets/video/array.mp4" type="video/mp4"></video>

      <v-fade-transition>
          <v-overlay
            v-if="hover"
            absolute
            color="#555555"
          >
          <v-btn :color="color"> </v-btn>
          </v-overlay>
      </v-fade-transition>
     
    </v-card>
    </template>
    </v-hover>

  <br><br>
  </v-col>
</template>

<script>
export default {
  data: () => {
    return {
      overlay: false,
      color: 'blue',
    };
  },
  props: {
    basic: Object,
  },
  methods: {
    moveTopic() {
      //   
      this.$router.push({
        name: 'Basic',
        params: {
          topic: this.basic.topic,
          name: this.basic.name,
        },
      });
    },
  },
  created(){
    //    
    if(this.basic.name == '' || this.basic.name == 'if ')this.color='#6F85FF';
    if(this.basic.name == '' || this.basic.name == '')this.color='#FF4D55';
    if(this.basic.name == '' || this.basic.name == 'for ')this.color='#FFB911';
    if(this.basic.name == '' || this.basic.name == '1 ')this.color='#6CB832';
  }
};
</script>

<style>
</style>