<template>
  <v-container fill-height fluid>
    <v-row no-gutters justify="center" align="center">
      <v-col cols="12" sm="4">
        <div align="center"><v-img width="70px" src="@/assets/img/codackji_logo.png" /></div>
        <h1 class="text--primary pt-3"></h1>
        <v-form persistent ref="form">
          <v-text-field
            class="pl-3 pr-3"
            :rules="nameRules"
            :counter="10"
            v-model="name"
            label=""
            type="text"
            prepend-icon="mdi-account"
          ></v-text-field>
          <v-text-field
            class="pl-3 pr-3"
            :rules="emailRules"
            v-model="email"
            label=""
            type="email"
            prepend-icon="mdi-email"
          ></v-text-field>
          <v-text-field
            class="pl-3 pr-3"
            :rules="passwordRules"
            v-model="password"
            label=""
            :counter="16"
            type="password"
            prepend-icon="mdi-lock"
          ></v-text-field>
          <v-text-field
            class="pl-3 pr-3"
            :rules="[validatePasswordRules, passwordConfirmationRule]"
            v-model="validatePassword"
            label=" "
            type="password"
            prepend-icon="mdi-lock"
          ></v-text-field>
          <v-chip class="ma-2" color="primary" outlined pill @click="joinRequest">
            
            <v-icon right>
              mdi-account-outline
            </v-icon>
          </v-chip>
          <v-chip class="ma-2" close color="red darken-3" outlined @click="reset">
            
          </v-chip>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;
const SECRET_KEY = process.env.VUE_APP_SECRET_KEY;

export default {
  name: 'SignUp',
  computed: {
    validatePasswordRules() {
      return () => this.validatePassword != null || '  .';
    },
    passwordConfirmationRule() {
      return () => this.password === this.validatePassword || '  .';
    },
  },
  data: () => ({
    name: '',
    email: '',
    password: '',
    validatePassword: '',
    nameRules: [
      (v) => !!v || ' .',
      (v) => (v && v.length <= 10) || ' 10  ',
    ],
    emailRules: [
      (v) => !!v || '  .',
      (v) => /.+@.+\..+/.test(v) || '    .',
    ],
    passwordRules: [
      (v) => !!v || ' .',
      (v) =>
        /^(?=.*[a-z])(?=.*[0-9]).{8,16}$/.test(v) || ',   8-16  ',
    ],
  }),
  methods: {
    reset() {
      this.$refs.form.reset();
    },
    joinRequest() {
      const crypto = require('crypto');
      if (this.$refs.form.validate()) {
        const hash = crypto
          .createHmac('sha256', SECRET_KEY)
          .update(this.password)
          .digest('base64');
        axios
          .post(`${SERVER_URL}/user/confirm/add`, {
            name: this.name,
            email: this.email,
            password: hash,
          })
          .then(({ data }) => {
            let msg = ' .';
            if (data === 'success') {
              msg = '  .';
              this.$router.push({ name: 'Home' });
            }
            alert(msg);
          })
          .catch(() => {
            alert('   .');
          });
        this.$refs.form.reset();
      }
    },
  },
};
</script>

<style></style>
