<template>
  <div>
    <v-container fluid>
      <h3 v-if="userInfo.stat == ''" style="font-size:1.5em">
        <v-icon color="brown">mdi-paw</v-icon>   
        <v-icon color="brown">mdi-paw</v-icon> <br /><br />
        <v-btn color="#6f85ff" @click="isDialog = true">
          <v-icon color="white">mdi-help-rhombus</v-icon
          ><span style="color:white"></span>
        </v-btn>
      </h3>
      <h3 v-else>
        <v-icon color="brown">mdi-paw</v-icon>    
        <v-icon color="brown">mdi-paw</v-icon>
      </h3>

      <br /><br /><br />

      <!--   :    -->
      <template v-if="userInfo.stat == ''">
        <v-row style="background-color:#FFFFCC">
          <v-col cols="3" style="margin-left:-5%"
            ><h3 style="color:#6F85FF"></h3></v-col
          >
          <v-col cols="4" style="margin-left:-5%"
            ><h3 style="color:#6CB832"></h3></v-col
          >
          <v-col cols="2" style="margin-left:2%"
            ><h3 style="color:#FFB911"> </h3></v-col
          >
          <v-col cols="2" style="margin-left:-1%"
            ><h3 style="color:#FF4D55"> </h3></v-col
          >
          <v-col cols="1" style="margin-left:3%"
            ><h3 style="color:#6F85FF"> </h3></v-col
          >
        </v-row>
        <br />
        <v-expansion-panels popout>
          <v-expansion-panel
            v-for="(question, i) in questionList"
            :key="i"
            hide-actions
            @click="rowClick(question)"
          >
            <v-expansion-panel-header
              style="border: 1px solid rgba(10,10,0,0.12)"
            >
              <v-row align="center" class="spacer" no-gutters>
                <!--  -->
                <v-col cols="3">
                  {{ question.question_title.substring(0, 10) }}
                  <span v-if="question.question_title.length > 10">...</span>
                </v-col>
                <!--  -->
                <v-col cols="4">
                  {{ question.question_content.substring(0, 20) }}
                  <span v-if="question.question_content.length > 20">...</span>
                </v-col>
                <!--   -->
                <v-col cols="2">
                  {{ question.created_at.substring(0, 10) }}
                </v-col>
                <!--   -->
                <v-col cols="2">
                  <span v-if="question.problem_number == null || question.problem_number == 0">  </span>
                  <span v-else>
                    [{{ question.problem_number }}]
                    {{ question.problem_title }}</span
                  >
                </v-col>
                <!--   -->
                <v-col cols="1">
                  <span v-if="question.question_answered"
                    ><v-icon color="#FFB911" size="16px"
                      >mdi-paw</v-icon
                    ></span
                  >
                  <span v-else> </span>
                </v-col>
              </v-row>
            </v-expansion-panel-header>

            <v-expansion-panel-content>
              <v-divider></v-divider>
              <!--  -->
              <br />
              <v-simple-table style="width:50%; margin-left:25%;">
                <tr style="height:60px">
                  <td
                    style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD;border-top: 1px solid #DDDDDD"
                    width="20%"
                  >
                    
                  </td>
                  <td
                    style="background-color:#effffc;border-bottom: 1px solid #DDDDDD;border-top: 1px solid #DDDDDD"
                  >
                    {{ question.question_title }}
                  </td>
                </tr>
                <tr style="height:60px">
                  <td
                    style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD"
                  >
                    
                  </td>
                  <td
                    style="background-color:#effffc;border-bottom: 1px solid #DDDDDD"
                  >
                    {{ question.question_content }}
                  </td>
                </tr>
                <tr style="height:60px" v-if="question.problem_number != null && question.problem_number != 0">
                  <template >
                    <td
                      style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD"
                    >
                       
                    </td>
                    <td
                      style="background-color:#effffc;border-bottom: 1px solid #DDDDDD; cursor:pointer"
                    >
                      [{{ question.problem_number }}] {{ question.problem_title
                      }}<v-icon color="#FFB911"
                        >mdi-arrow-right-bold-box</v-icon
                      >
                    </td>
                  </template>
                </tr>
                <tr style="height:60px">
                  <template v-if="question.question_reply != null">
                    <td
                      style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD"
                    >
                      
                    </td>
                    <td
                      style="background-color:#effffc;border-bottom: 1px solid #DDDDDD"
                    >
                      {{ question.question_reply }}
                    </td>
                  </template>
                </tr>
              </v-simple-table>

              <template v-if="question.question_reply == null">
                <span> "    "</span>
                <br /><br />
                <v-btn text @click="deleteQuestion" style="font-size:1em">
                  |<v-icon color="#555555" size="20">mdi-delete-outline</v-icon>
                    |
                </v-btn>
              </template>
              <template v-else>
                <v-card
                  width="30%"
                  min-height="100px"
                  style="margin-left:35%">
                  <v-card-text>
                    <!-- " , "    :) -->
                    <img :src="question.teach_profile" width="20%"/> <h2>{{question.teach_name}}</h2> 
                      :)                    
                  </v-card-text>
                </v-card>
              </template>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </template>

      <!--   :   ,     . -->
      <template v-else>
        <v-row style="background-color:#FFFFCC">
          <v-col cols="3"><h3 style="color:#6F85FF"> </h3></v-col>
          <v-col cols="2" style="margin-left: -5%"
            ><h3 style="color:#6CB832"></h3></v-col
          >
          <v-col cols="4" style="margin-left: -5%"
            ><h3 style="color:#FFB911"> </h3></v-col
          >
          <v-col cols="2" style="margin-left: 1%"
            ><h3 style="color:#FF4D55"> </h3></v-col
          >
          <v-col cols="1" style="margin-left: 3%"
            ><h3 style="color:#6F85FF"> </h3></v-col
          >
        </v-row>
        <br />
        <v-expansion-panels popout>
          <v-expansion-panel
            v-for="(question, i) in questionList"
            :key="i"
            hide-actions
            @click="rowClick(question)"
          >
            <v-expansion-panel-header
              style="border: 1px solid rgba(10,10,0,0.12)"
            >
              <v-row align="center" class="spacer" no-gutters>
                <v-col cols="3">
                  <!--  ,  -->
                  <img :src="question.profile_content" width="10%"/><h3>{{question.name}}</h3>
                </v-col>

                <!--  -->
                <v-col cols="2">
                  {{ question.question_title.substring(0, 10) }}
                  <span v-if="question.question_title.length > 10">...</span>
                </v-col>

                <!-- -->
                <v-col cols="4">
                  {{ question.question_content.substring(0, 20) }}
                  <span v-if="question.question_content.length > 20">...</span>
                </v-col>

                <!--   -->
                <v-col cols="2">
                  {{ question.created_at.substring(0, 10) }}
                </v-col>

                <!--  -->
                <v-col cols="1">
                  <span v-if="question.question_answered"
                    ><v-icon color="#FFB911" size="16px"
                      >mdi-paw</v-icon
                    ></span
                  >
                  <span v-else> </span>
                </v-col>
              </v-row>
            </v-expansion-panel-header>

            <v-expansion-panel-content>
              <v-divider></v-divider>
              <!--  -->
              <br />
              <v-simple-table style="width:50%; margin-left:25%;">
                <tr style="height:60px">
                  <td
                    style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD;border-top: 1px solid #DDDDDD"
                    width="20%"
                  >
                    
                  </td>
                  <td
                    style="background-color:#effffc;border-bottom: 1px solid #DDDDDD;border-top: 1px solid #DDDDDD"
                  >
                    {{ question.question_title }}
                  </td>
                </tr>
                <tr style="height:60px">
                  <td
                    style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD"
                  >
                    
                  </td>
                  <td
                    style="background-color:#effffc;border-bottom: 1px solid #DDDDDD"
                  >
                    {{ question.question_content }}
                  </td>
                </tr>
                <tr style="height:60px" v-if="question.problem_number != null && question.problem_number != 0">
                  <template >
                    <td
                      style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD"
                    >
                       
                    </td>
                    <td
                      style="background-color:#effffc;border-bottom: 1px solid #DDDDDD; cursor:pointer"
                    >
                      [{{ question.problem_number }}] {{ question.problem_title
                      }}<v-icon color="#FFB911"
                        >mdi-arrow-right-bold-box</v-icon
                      >
                    </td>
                  </template>
                </tr>
                <tr style="height:60px">
                  <template v-if="question.question_reply != null">
                    <td
                      style="background-color:#eff1ff;border-bottom: 1px solid #DDDDDD"
                    >
                      
                    </td>
                    <td
                      style="background-color:#effffc;border-bottom: 1px solid #DDDDDD"
                    >
                      {{ question.question_reply }}
                    </td>
                  </template>
                </tr>
              </v-simple-table>

              <template v-if="question.question_reply == null">
                <span v-if="userInfo.stat.includes('')">
                  <v-btn
                    elevation="1"
                    style="background-color:#FFCA22"
                    @click="isDialog = true"
                  >
                    <v-icon color="red">mdi-pen-plus</v-icon> </v-btn
                  ></span
                >
              </template>
              <template v-else>
                <br /><br />
                <v-card width="30%" min-height="100px" style="margin-left:35%">
                  <!-- " , "    :) -->
                    <img :src="question.teach_profile" width="20%"/> <h2>{{question.teach_name}}</h2> 
                      :) 
                </v-card>
                <br /><br />
              </template>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </template>
    </v-container>

    <!--   -->
    <v-dialog v-model="isDialog" width="50%">
      <v-card min-height="500px" style="position: relative">
        <v-card-title style="background-color:#CCFFCC">
          <img src="@/assets/img/codackji_logo.png" height="30px" />
        </v-card-title>

        <v-card-text v-if="userInfo.stat == ''" style="font-size:1.2em;">
          <br />
          <v-textarea
            label=""
            auto-grow
            outlined
            rows="1"
            row-height="10"
            v-model="title"
          ></v-textarea>
          <v-textarea
            label=" "
            auto-grow
            outlined
            rows="10"
            row-height="40"
            v-model="content"
          ></v-textarea>
        </v-card-text>

        <v-card-text v-else style="font-size:1.2em;">
          <br />
          <v-textarea
            label=" "
            auto-grow
            outlined
            rows="10"
            row-height="40"
            v-model="content"
          ></v-textarea>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            @click="cancelQuestion"
            style="font-size:1.4em"
          >
            <v-icon color="#FFB911" size="20" @click="cancelQuestion"
              >mdi-close-thick</v-icon
            >
            
          </v-btn>
          <v-btn @click="upload" style="font-size:1.4em" color="primary">
            <v-icon color="#FFB911" size="20">mdi-lead-pencil</v-icon>
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from 'axios';

const SERVER_URL = process.env.VUE_APP_SERVER_URL;

export default {
  name: 'MyQuestion',
  props: {
    userInfo: Object,
  },
  data: () => {
    return {
      questionList: [],
      isDialog: false,
      title: '',
      content: '',
      selected: [],
    };
  },
  methods: {
    rowClick(item){
      this.selected = item;
    },
    upload() {
      if (this.userInfo.stat == '') {
        //  
        if(confirm(' ?')){
        axios
          .post(`${SERVER_URL}/question/write`, {
            token: localStorage.getItem('jwt'),
            user_number: this.userInfo.number,
            question_title: this.title,
            question_content: this.content,
          })
          .then((response) => {
            console.log('response');
            alert(' !');
            console.log(response);
            this.setQuestionList();            
            this.cancelQuestion();
          })
          .catch((error) => {
            alert('error!');
            console.log(error);
          });
        }
      } else {
        //  
        axios
          .put(`${SERVER_URL}/answer/write`, {
            token: localStorage.getItem('jwt'),
            teach_user_number: this.userInfo.number,
            question_number: this.selected.question_number,
            question_reply: this.content
          })
          .then((response) => {
            console.log('response');
            alert(' !');
            console.log(response);
            location.reload();
          })
          .catch((error) => {
            alert('error!');
            console.log(error);
          });
      }
    },
    cancelQuestion() {
      //alert('');
      this.title = '';
      this.content = '';
      this.isDialog = false;
    },
    deleteQuestion() {
      //    
      if(confirm(' ?')){
        axios
          .delete(`${SERVER_URL}/question`, {
            data:{
            token: localStorage.getItem('jwt'),
            question_number : this.selected.question_number
            }
          })
          .then((response) => {
            console.log('response');
            alert(' !');
            console.log(response);
            location.reload();
          })
          .catch((error) => {
            alert('error!');
            console.log(error);
          });
      }
    },
    setQuestionList() {
      if (this.userInfo.stat == '') {
        //  
        axios
          .post(`${SERVER_URL}/question`, {
            token: localStorage.getItem('jwt'),
          })
          .then((response) => {
            console.log('');
            console.log(response.data);
            this.questionList = response.data;
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        //  
        axios
          .post(`${SERVER_URL}/question/all`, {
            token: localStorage.getItem('jwt'),
          })
          .then((response) => {
            console.log('');
            console.log(response.data);
            this.questionList = response.data;
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
  },
  created() {
    this.setQuestionList();
  },
};
</script>

<style></style>
